<script>
import NavBar from "../components/navbar";
import FirstPage from "../components/firstpage";
import ContactUs from "../components/contactus";
import Footer from "../components/footer";
import { markers } from "../store/markers.js";
import { specs, extras, extras_imgs, specs_maarina, extras_maarina } from "../store/specs.js";

export default {
  components: {
    NavBar,
    FirstPage,
    ContactUs,
    Footer
  },
  data() {
    return {
      langs: [
        {
          id: 0,
          abr: "CA",
          src: require("../assets/lang/ca.png")
        },
        {
          id: 1,
          abr: "ES",
          src: require("../assets/lang/esp.png")
        },
        {
          id: 2,
          abr: "FR",
          src: require("../assets/lang/fr.png")
        },
        {
          id: 3,
          abr: "EN",
          src: require("../assets/lang/eng.png")
        }
      ],
      imgs: [
        { src: "expeditions/second.jpg" },
        { src: "expeditions/third.jpg" },
        { src: "expeditions/fourth.jpg" },
        { src: "expeditions/first.jpg" }
      ],
      markers: markers,
      markertxt: "Foradada",
      markersrc: ["foradada1.jpg"],
      timer: 0,
      currentimg: 0,
      specs: specs,
      extras: extras,
      extras_imgs: extras_imgs,
      specs_maarina: specs_maarina,
      extras_maarina: extras_maarina,
      txtlen: 1,
      wake: this.$t('exp.altres.wake_text'),
      banana: this.$t('exp.altres.banana_text'),
      torpedo: this.$t('exp.altres.torpedo_text'),
      concerts: this.$t('exp.altres.concerts_text'),
      focs: this.$t('exp.altres.focs_text'),
      aniversari: this.$t('exp.altres.aniversari_text'),
      despedida: this.$t('exp.altres.despedida_text'),
      restaurants: this.$t('exp.altres.restaurants_text')
    };
  },
  methods: {
    minfo(m) {
      this.markersrc = m.src;
      this.markertxt = m.label.text;
      var items = document.getElementsByClassName("loc_item");
      for (let ii = 0; ii < items.length; ii++) {
        items[ii].children[0].style.opacity = 0;
        items[ii].children[1].children[0].style.backgroundColor =
          "rgba(42, 81, 111,1)";
        items[ii].children[2].style.color = "black";
        items[ii].children[2].style.fontSize = "0.6rem";
      }
      event.currentTarget.children[0].style.opacity = 1;
      event.currentTarget.children[1].children[0].style.backgroundColor =
        "rgba(203, 128, 52,1)";
      event.currentTarget.children[2].style.color = "rgba(142, 89, 37,1)";
      event.currentTarget.children[2].style.fontSize = "0.7rem";
    },
    changeimg() {
      var mapimgs = document.getElementsByClassName("mapimg");
      var dots = document.getElementsByClassName("dot_map");
      if (this.currentimg >= mapimgs.length) {
        this.currentimg = 0;
      }
      for (let ii = 0; ii < mapimgs.length; ii++) {
        if (ii == this.currentimg) {
          mapimgs[ii].style.display = "block";
          dots[ii].style.backgroundColor = "white";
        } else {
          mapimgs[ii].style.display = "none";
          dots[ii].style.backgroundColor = "rgba(255, 255, 255, 0.4)";
        }
      }
      this.currentimg = this.currentimg + 1;
    },
    sliding(imgs) {
      clearInterval(this.timer);
      if (imgs.length > 1) {
        this.currentimg = 0;
        setTimeout(this.changeimg, 50);
        this.timer = setInterval(this.changeimg, 3000);
      } else {
        document.getElementsByClassName("mapimg")[0].style.display = "block";
      }
    },
    show(item,btn_item){
      var reservaUI = document.getElementById(item); 
      let btn = document.getElementById(btn_item); 
      if (reservaUI.style.display == '' || reservaUI.style.display == 'none') {
        reservaUI.style.display = 'block';
        btn.innerHTML = '- '+this.$t('exp.reservar');
      } else {
        reservaUI.style.display = 'none';
        btn.innerHTML = '+ '+this.$t('exp.reservar');
      }
    },
    showMore() {
      var more = event.target.parentElement.querySelector('.more');
      var info_text = event.currentTarget;
      if (more.style.display == '' || more.style.display == 'none') {
        more.style.display = 'block';
        info_text.innerHTML = this.$t('home.readless');
      } else {
        more.style.display = 'none';
        info_text.innerHTML = this.$t('home.readmore');
      }
    }
  },
  computed: {
    comp_langs: function() {
      var langs1 = [];
      var j = 0;
      for (let i = 0; i < this.langs.length; i++) {
        if (this.$i18n.locale != this.langs[i].abr.toLowerCase()) {
          langs1[j] = {};
          langs1[j].id = j + 1;
          langs1[j].text = this.$t("langs." + this.langs[i].abr.toLowerCase());
          langs1[j].abr = this.langs[i].abr.toLowerCase();
          langs1[j].src = this.langs[i].src;
          j++;
        }
      }
      return langs1;
    }
  },
  mounted() {
    if (window.screen.availWidth < 400) {
      this.txtlen = 0
    } else {
      this.txtlen = 1
    }
    const sliders_left = document.querySelectorAll('.from_left');
    const sliders_right = document.querySelectorAll('.from_right');

    const appearOptions = {
        threshold: 0,
        rootMargin: "0px 0px -290px 0px"
    };

    const appearOnScroll = new IntersectionObserver(function(entries, appearOnScroll) {
        entries.forEach(entry => {
            if (!entry.isIntersecting) {
                return;
            } else {
                entry.target.classList.add('appear');
                appearOnScroll.unobserve(entry.target);
            }
        })
    },
    appearOptions);


    sliders_left.forEach(slider => {
        appearOnScroll.observe(slider);
    })

    sliders_right.forEach(slider => {
        appearOnScroll.observe(slider);
    })
  }
};
</script>
