<template>
  <nav
    @mouseover="highlight('rgba(14, 27, 37, 1)')"
    @mouseleave="unhighlight('rgba(21, 42, 55, 0.8)')"
  >
    <div id="navbar">
      <div class="bars_div">
        <font-awesome-icon
          @click="toggle('rgb(29, 53, 73)')"
          class="bars"
          :icon="['fas', 'bars']"
        />  
      </div>
      <div id="logo">
        <a href="./">
          <img :src="require(`../assets/logos/${logo}`)" alt="Maar logo" />
        </a>
      </div>
      <div id="nav-items" ref="navitems">
        <div
          class="item"
          v-for="(item, idx) in items"
          :key="idx"
          @mouseover="Htext(theme, '1.05em')"
          @mouseleave="Utext('white', '1em')"
        >
          <div v-if="!Array.isArray(item)" @click="scroll(scrollto[idx],1)">
            <router-link class="link" v-if="router_link==1" v-bind:to="`/${$i18n.locale}` + scrollto[idx]">
            {{ item }}
            </router-link>
            <div v-else> {{item}} </div>
          </div>
          <div v-else>
            <div
              @mouseover="Show('subitems',item[0])"
              @mouseleave="Hide('subitems',item[0])"
              class="subitem"
            >
            <div @click="scroll(scrollto[idx][0],0), MobileShowSubitems()">{{ item[1]}}</div>
              <div class="subitems" :style="'background-color:' + dropdwcolor">
                <div v-for="(subitem,idxx) in item.slice(2)" :key="idxx">
                  <div 
                    class="subitems_text"
                    @mouseover="highlight(dropdwcolor_text)"
                    @mouseleave="unhighlight('transparent')"
                    @click="scroll(scrollto[idx][idxx+1],1)"
                  >{{subitem}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        id="lang"
        @mouseover="Hlang(theme), Show('lang-content',0)"
        @mouseleave="Ulang('rgba(150,150,150,0.8)'), Hide('lang-content',0)"
      >
        <div class="lang-btn">
          <div class="globe">
            <font-awesome-icon :icon="['fas', 'globe']" />
          </div>
          <div class="lang_sel">
            <p>
              {{ lng }}
              <span style="font-size: 0.5em; vertical-align: top;">&#9660;</span>
            </p>
          </div>
        </div>
        <div
          :style="'background-color:' + dropdwcolor"
          class="lang-content"
          ref="dropdwn"
        >
          <div
            class="lang-item"
            v-for="lang in langs"
            :key="lang.id"
            @click="setLocale(lang.abr)"
            @mouseover="highlight(dropdwcolor_text)"
            @mouseleave="unhighlight('transparent')"
          >
            <div class="flag">
              <img :src="lang.src" alt="Lang" />
            </div>
            <p>{{ lang.text }}</p>
          </div>
        </div>
      </div>
      <div id="social_media">
        <div>
          <font-awesome-icon
            :style="style420"
            @mouseover="Htext('white', '1.35em')"
            @mouseleave="Utext(theme, '1.2em')"
            :icon="['fab', 'instagram']"
          />
        </div>
        <div>
          <font-awesome-icon
            :style="style420"
            @mouseover="Htext('white', '1.35em')"
            @mouseleave="Utext(theme, '1.2em')"
            :icon="['fab', 'facebook']"
          />
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  props: ["items","scrollto", "logo", "lng", "langs", "theme", "dropdwcolor","dropdwcolor_text","router_link"],
  data() {
    return {
      style420: {
        color: this.theme
      },
      count: -1
    };
  },
  methods: {
    highlight(color) {
      event.currentTarget.style.backgroundColor = color;
    },
    unhighlight(color) {
      event.currentTarget.style.backgroundColor = color;
    },
    Htext(color, size) {
      event.currentTarget.style.color = color;
      event.currentTarget.style.fontSize = size;
    },
    Utext(color, size) {
      event.currentTarget.style.color = color;
      event.currentTarget.style.fontSize = size;
    },
    Hlang(color) {
      event.currentTarget.children[0].children[0].children[0].style.color = color;
    },
    Ulang(color) {
      event.currentTarget.children[0].children[0].children[0].style.color = color;
    },
    Show(item,ii) {
      var el = document.getElementsByClassName(item)[ii];
      el.style.visibility = "visible";
      el.style.opacity = 1;
    },
    Hide(item,ii) {
      var el = document.getElementsByClassName(item)[ii];
      el.style.visibility = "hidden";
      el.style.opacity = 0;
    },
    MobileShowSubitems() {
      if (screen.width < 680) {
        var subitem = event.currentTarget.nextSibling;
        subitem.style.display = "block";
      }
    },
    setLocale(locale) {
      this.$i18n.locale = locale;
      //Update url
      this.$router.push({ params: { lang: locale } });
      var dropdw = this.$refs["dropdwn"].style;
      if (dropdw.opacity == "0") {
        dropdw.opacity = "1";
        dropdw.visibility = "visible";
      } else {
        dropdw.opacity = "0";
        dropdw.visibility = "hidden";
      }
    },
    toggle(color) {
      var nav = this.$refs["navitems"].style;
      var lang = document.getElementById('lang');
      nav.backgroundColor = color;
      if (nav.display == "none") {
        nav.display = "inline-block";
        lang.style.display = "none";
      } else {
        nav.display = "none";
        lang.style.display = "block";
      }
    },
    scroll(item,bool) {
      var element = document.getElementById(item);
      if (screen.width < 690) {
        if (bool == 1) {
          element.scrollIntoView({behavior: 'smooth', block: 'start'});
          var nav = this.$refs["navitems"].style;
          nav.display = "none";
        }
      } else {
        element.scrollIntoView({behavior: 'smooth', block: 'start'});
      }
    }
  }
};
</script>

