<script>
import NavBar from "../components/navbar";
import FirstPage from "../components/firstpage";
import Footer from "../components/footer";
import ContactUs from "../components/contactus";

export default {
  name: "Teach",
  components: {
    NavBar,
    FirstPage,
    ContactUs,
    Footer
  },
  data() {
    return {
      langs: [
        {
          id: 0,
          text: "Catala",
          abr: "CA",
          src: require("../assets/lang/ca.png")
        },
        {
          id: 1,
          text: "Spanish",
          abr: "ES",
          src: require("../assets/lang/esp.png")
        },
        {
          id: 2,
          text: "Frances",
          abr: "FR",
          src: require("../assets/lang/fr.png")
        },
        {
          id: 3,
          text: "Ingles",
          abr: "EN",
          src: require("../assets/lang/eng.png")
        }
      ],
      imgs: [
        { src: "teach/teach.jpeg" },
      ],
      selection: -1,
    };
  },
  methods: {
    emphasise() {
      var service_img = event.currentTarget.children[0];
      var service_title = event.currentTarget.children[1];
      var service_text = event.currentTarget.children[2];
      var service_more = event.currentTarget.children[3];
      service_img.style.webkitFilter = "invert(70%)";
      if (screen.width > 600) {service_more.innerHTML = " Ms informaci ";} else {service_more.innerHTML = "+";}
      service_more.style.textDecoration = "underline";
      service_more.style.color = "#ebadad";
      service_more.style.fontSize = "1rem";
      service_title.style.color = "white";
      service_text.style.color = "#f5d6d6";
    },
    noemphasise() {
      var service_img = event.currentTarget.children[0];
      service_img.style.webkitFilter = "invert(50%)";
      var service_more = event.currentTarget.children[3];
      service_more.innerHTML = "+";
      service_more.style.textDecoration = "none";
      service_more.style.color = "black";
      service_more.style.fontSize = "1rem";
      var service_title = event.currentTarget.children[1];
      service_title.style.color = "rgba(0,0,0,0.8)";
      var service_text = event.currentTarget.children[2];
      service_text.style.color = "rgba(0,0,0,0.8)";
    },
    showinfo(idx) {
      this.selection = idx;
      var info = document.getElementById("info");
      var info_more = event.currentTarget; 
      var transitions = document.getElementsByClassName("wave_transition");
      var transitions1 = document.getElementsByClassName("wave_transition2");
      var transition_block = document.getElementsByClassName("transition_block");
      var services = document.getElementsByClassName("service");
      for (let ii = 0; ii < 3; ii++) {
        if(ii == idx) {
          services[idx].style.background = "var(--infocolor)";
          services[idx].style.transform= "scale(1.08)";
          if (screen.width > 600) {info_more.innerHTML = " Ms informaci ";} else {info_more.innerHTML = "+";}
          transitions[idx].style.display = "block";
          transition_block[idx].style.opacity = "1";
          transitions1[idx].style.display = "block";
          info.style.display = "flex";
        } else {
          services[ii].style.backgroundColor = "transparent";
          services[ii].style.transform= "scale(1)";
          info_more.innerHTML = "+";
          transitions[ii].style.display = "none";
          transition_block[ii].style.opacity = "0";
          transitions1[ii].style.display = "none";
          info.style.display = "flex";
        }
      }
    }
  },
  computed: {
    comp_langs: function() {
      var langs1 = [];
      var j = 0;
      for (let i = 0; i < this.langs.length; i++) {
        if (this.$i18n.locale != this.langs[i].abr.toLowerCase()) {
          langs1[j] = {};
          langs1[j].id = j + 1;
          langs1[j].text = this.langs[i].text;
          langs1[j].abr = this.langs[i].abr.toLowerCase();
          langs1[j].src = this.langs[i].src;
          j++;
        }
      }
      return langs1;
    }
  }
};
</script>

