import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Expeditions from "../views/Expeditions.vue";
import Wash from "../views/Wash.vue";
import Teach from "../views/Teach.vue";
import Confirmation from "../views/Confirmation.vue"
import i18n from "../i18n";

Vue.use(VueRouter);

const routes = [
  { path: "/", redirect: `/${i18n.locale}` },
  { path: "/wash", redirect: `/${i18n.locale}/wash`},
  { path: "/expeditions", redirect: `/${i18n.locale}/expeditions`},
  { path: "/teach", redirect: `/${i18n.locale}/teach`},
  {
    path: "/:lang",
    component: {
      render(c) {
        return c("router-view");
      }
    },
    children: [
      {
        path: "/",
        name: "Home",
        component: Home
      },
      {
        path: "expeditions",
        name: "Expeditions",
        component: Expeditions
      },
      {
        path: "wash",
        name: "Wash",
        component: Wash
      },
      {
        path: "teach",
        name: "Teach",
        component: Teach
      },
      {
        path: "confirmation",
        name: "confirmation",
        component: Confirmation
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  console.log(to.name)
  if ((to.name=='Home') || (to.name==null)) {
    document.title = "Maar"
  } else {
    document.title = `Maar - ${to.name}`
  } 

  next()
})

export default router;
