<template>
  <button
    id="btn"
    type="button"
    :style="btnstyle"
    @mouseover="hbtn(MouseOn_Back,MouseOn_Shadow,MouseOn_Color)"
    @mouseleave="lbtn(MouseOff_Back,MouseOff_Shadow,MouseOff_Color)"
  >
    <span v-if="contact" style="margin-right: 5px;"
      ><font-awesome-icon :icon="['fas', 'phone']"
    /></span>
    {{ title }}
    <div id="types_contact" v-if="contact">
      <a
        @mouseover="hbtn(MouseOn_a_Back,'none',MouseOn_a_Color)"
        @mouseleave="lbtn(MouseOff_a_Back,'none',MouseOff_a_Color)"
        :style="astyle"
        target="_blank"
        href="https://api.whatsapp.com/send?phone=+34621242500"
        ><font-awesome-icon
          :icon="['fab', 'whatsapp']"
      /></a>
      <a 
        @mouseover="hbtn(MouseOn_a_Back,'none',MouseOn_a_Color)"
        @mouseleave="lbtn(MouseOff_a_Back,'none',MouseOff_a_Color)"
        :style="astyle"
        href=""><font-awesome-icon :icon="['fas', 'phone']"/></a>
      <a 
        @mouseover="hbtn(MouseOn_a_Back,'none',MouseOn_a_Color)"
        @mouseleave="lbtn(MouseOff_a_Back,'none',MouseOff_a_Color)"
        :style="astyle"
        href=""><font-awesome-icon :icon="['fa', 'envelope']"/></a>
    </div>
  </button>
</template>
<script>
export default {
  name: "Btn",
  props: ["title","contact","MouseOn_Back","MouseOn_Shadow","MouseOn_Color","MouseOff_Back","MouseOff_Shadow","MouseOff_Color","MouseOn_a_Back","MouseOn_a_Color","MouseOff_a_Back","MouseOff_a_Color"],
  data() {
    return {
      btnstyle: {
      color: this.MouseOff_Color,
      background: this.MouseOff_Back,
      boxShadow: this.MouseOff_Shadow
      },
      astyle: {
        color: this.MouseOff_a_Color,
        background: this.MouseOff_a_Back
      }
    }
  },
  methods: {
    hbtn(background,boxShadow,color) {
      var btn = event.currentTarget;
      btn.style.background = background;
      btn.style.boxShadow = boxShadow;
      btn.style.Tranform = "translate(-7px)";
      btn.style.color = color;
    },
    lbtn(background,boxShadow,color) {
      var btn = event.currentTarget;
      btn.style.background = background;
      btn.style.boxShadow = boxShadow;
      btn.style.Tranform = "translate(7px)";
      btn.style.color = color;
    }
  }
}
</script>
