<template>
  <div id="firstpage">
    <img
      v-for="(img, idx) in imgs"
      :key="idx"
      class="imgs"
      :src="require(`../assets/${img.src}`)"
      alt="Alquiler barco"
    />
    <div id="title">{{ title }}</div>
    <div class="next left" @click="more(-1, false)">
      <div class="arrow"><span>&#10094;</span></div>
    </div>
    <div class="next right" @click="more(1, false)">
      <div class="arrow"><span>&#10095;</span></div>
    </div>
    <div class="dots">
      <span v-for="(img, idx) in imgs" :key="idx" class="dot"></span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["imgs", "title"],
  data() {
    return {
      currentimg: 0,
      timer: 0
    };
  },
  methods: {
    more(n, auto) {
      var imgs = document.getElementsByClassName("imgs");
      var dots = document.getElementsByClassName("dot");
      this.currentimg = this.currentimg + n;
      if (this.currentimg >= imgs.length) {
        this.currentimg = 0;
      } else if (this.currentimg < 0) {
        this.currentimg = dots.length - 1;
      }
      for (let ii = 0; ii < imgs.length; ii++) {
        if (ii == this.currentimg) {
          imgs[ii].style.opacity = "1";
          dots[ii].style.backgroundColor = "white";
        } else {
          imgs[ii].style.opacity = "0";
          dots[ii].style.backgroundColor = "rgba(255, 255, 255, 0.4)";
        }
      }
      if (auto) {
        this.timer = this.auto(4000);
      } else {
        clearInterval(this.timer);
        this.timer = this.auto(4000);
      }
    },
    auto(time) {
      var timer = setInterval(() => {
        var imgs = document.getElementsByClassName("imgs");
        var dots = document.getElementsByClassName("dot");
        this.currentimg = this.currentimg + 1;
        if (this.currentimg >= imgs.length) {
          this.currentimg = 0;
        } else if (this.currentimg < 0) {
          this.currentimg = dots.length - 1;
        }
        for (let ii = 0; ii < imgs.length; ii++) {
          if (ii == this.currentimg) {
            imgs[ii].style.opacity = "1";
            dots[ii].style.backgroundColor = "white";
          } else {
            imgs[ii].style.opacity = "0";
            dots[ii].style.backgroundColor = "rgba(255, 255, 255, 0.4)";
          }
        }
      }, time);
      return timer;
    }
  },
  mounted() {
    this.more(0, true);
  }
};
</script>

