<script>
import NavBar from "../components/navbar";
import Footer from "../components/footer";
import FirstPage from "../components/firstpage";
import Btn from "../components/btn";
import ContactUs from "../components/contactus";

export default {
  name: "Wash",
  components: {
    NavBar,
    FirstPage,
    Footer,
    Btn,
    ContactUs
  },
  data() {
    return {
      langs: [
        {
          id: 0,
          abr: "CA",
          src: require("../assets/lang/ca.png")
        },
        {
          id: 1,
          abr: "ES",
          src: require("../assets/lang/esp.png")
        },
        {
          id: 2,
          abr: "FR",
          src: require("../assets/lang/fr.png")
        },
        {
          id: 3,
          abr: "EN",
          src: require("../assets/lang/eng.png")
        }
      ],
      imgs: [
        { src: "wash/1.jpeg" },
        { src: "wash/2.jpeg" },
        { src: "wash/3.jpeg" },
        { src: "wash/4.jpeg" }
      ],
      publicPath: process.env.BASE_URL,
      aprov1_text: this.$t('wash.aprov.aprov1_text')
    };
  },
  methods: {
    emphasise() {
      var service_img = event.currentTarget.children[0];
      var service_title = event.currentTarget.children[1];
      var service_text = event.currentTarget.children[2];
      var service_more = event.currentTarget.children[3];
      service_img.style.webkitFilter = "invert(100%)";
      service_more.innerHTML = " Ms informaci ";
      service_more.style.textDecoration = "underline";
      service_more.style.color = "#246175";
      service_more.style.fontSize = "1rem";
      service_title.style.color = "#4fabc9";
      service_text.style.color = "rgba(255,255,255,0.7)";
    },
    noemphasise() {
      var service_img = event.currentTarget.children[0];
      service_img.style.webkitFilter = "invert(50%)";
      var service_more = event.currentTarget.children[3];
      service_more.innerHTML = "+";
      service_more.style.textDecoration = "none";
      service_more.style.color = "black";
      service_more.style.fontSize = "1.5rem";
      var service_title = event.currentTarget.children[1];
      service_title.style.color = "rgba(0,0,0,0.8)";
      var service_text = event.currentTarget.children[2];
      service_text.style.color = "rgba(0,0,0,0.8)";
    },
    showTable() {
      var table = document.getElementById("packs_info");
      var btn = document.getElementById("btn_show");
      if (table.style.opacity == "1") {
        table.style.opacity = "0";
        table.style.maxHeight = "0px";
        btn.innerHTML = "+ info";
      } else {
        table.style.maxHeight = "1000px";
        table.style.opacity = "1";
        btn.innerHTML = "- info";
      }
    },
    hbtn(sw,background,boxShadow,color) {
      var btn = event.currentTarget;
      var btn_show = document.getElementById("btn_show");
      if (sw == 1) {
        btn = btn_show;
      }
      btn.style.background = background;
      btn.style.boxShadow = boxShadow;
      btn.style.Tranform = "translate(-7px)";
      btn.style.color = color;
    },
    lbtn(sw,background,boxShadow,color) {
      var btn = event.currentTarget;
      var btn_show = document.getElementById("btn_show");
      if (sw == 1) {
        btn = btn_show;
      }
      btn.style.background = background;
      btn.style.boxShadow = boxShadow;
      btn.style.Tranform = "translate(7px)";
      btn.style.color = color;
    },
    scroll(item) {
      var element = document.getElementById(item);
      element.scrollIntoView({behavior: 'smooth', block: 'start'});
    }
  },
  computed: {
    comp_langs: function() {
      var langs1 = [];
      var j = 0;
      for (let i = 0; i < this.langs.length; i++) {
        if (this.$i18n.locale != this.langs[i].abr.toLowerCase()) {
          langs1[j] = {};
          langs1[j].id = j + 1;
          langs1[j].text = this.$t("langs." + this.langs[i].abr.toLowerCase());
          langs1[j].abr = this.langs[i].abr.toLowerCase();
          langs1[j].src = this.langs[i].src;
          j++;
        }
      }
      return langs1;
    }
  },
  watch: {
    comp_langs: {
      handler: function () {
        this.aprov1_text = this.$t('wash.aprov.aprov1_text');
      }
    } 
  },
  mounted() {
    const sliders_left = document.querySelectorAll('.from_left');
    const sliders_right = document.querySelectorAll('.from_right');

    const appearOptions = {
        threshold: 0,
        rootMargin: "0px 0px -290px 0px"
    };

    const appearOnScroll = new IntersectionObserver(function(entries, appearOnScroll) {
        entries.forEach(entry => {
            if (!entry.isIntersecting) {
                return;
            } else {
                entry.target.classList.add('appear');
                appearOnScroll.unobserve(entry.target);
            }
        })
    },
    appearOptions);


    sliders_left.forEach(slider => {
        appearOnScroll.observe(slider);
    })

    sliders_right.forEach(slider => {
        appearOnScroll.observe(slider);
    })
  }
};
</script>
