<template>
  <div id="contactus">
    <div class="wash_sep1">
      <svg
        data-name="Layer 1"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 1200 120"
        preserveAspectRatio="none"
        :style="'background: '+ WVColor"
      >
        <path
          d="M985.66,92.83C906.67,72,823.78,31,743.84,14.19c-82.26-17.34-168.06-16.33-250.45.39-57.84,11.73-114,31.07-172,41.86A600.21,600.21,0,0,1,0,27.35V120H1200V95.8C1132.19,118.92,1055.71,111.31,985.66,92.83Z"
          style="fill: rgb(21, 41, 55);"
        ></path>
      </svg>
    </div>
    <div id="contactus_div">
      <div class="section_logo">
        <font-awesome-icon :icon="['fa', 'question-circle']" />
      </div>
      <h2>{{ $t("exp.contact_title") }}</h2>
      <form id="contact_form" @submit.prevent="checkform()">
        <div id="send_status"></div>
        <div id="errors">
          <div class="error" v-for="(error, idx) in errors" :key="idx">
            {{ error }}
          </div>
        </div>
        <div class="contact_name">
          <label for="name">{{ $t("contactus.name") }}</label
          ><br />
          <input
            type="text"
            id="name"
            v-model="name"
            name="name"
            class="form-control"
          />
        </div>
        <div class="contact_surname">
          <label for="surname">{{ $t("contactus.surname") }}</label
          ><br />
          <input
            type="text"
            id="surname"
            v-model="surname"
            class="form-control"
          />
        </div>
        <div class="contact_mail">
          <label for="mail">{{ $t("contactus.mail") }}</label>
          <input type="text" id="mail" v-model="mail" class="form-control" />
        </div>
        <div class="contact_subject">
          <label for="subject">{{ $t("contactus.subject") }}</label>
          <input
            type="text"
            id="subject"
            v-model="subject"
            class="form-control"
          />
        </div>
        <br />
        <div>
          <textarea
            id="message"
            class="text_input"
            v-model="message"
            style="width: 100%; height: 190px; border-radius: 6px; color: black; outline: none; padding: 10px 10px; color: #333333"
          ></textarea>
        </div>
        <input id="send_mail" :style="'background:' + Theme" type="submit" :value="$t('contactus.send')" @mouseover="BtnHover(1)" @mouseleave="BtnHover(0)" />
      </form>
    </div>
  </div>
</template>
<script>
import emailjs from "emailjs-com";

export default {
  props: ["Theme", "HoverTheme","WVColor"],
  data() {
    return {
      errors: [],
      name: null,
      surname: null,
      mail: null,
      subject: null,
      message: null
    };
  },
  methods: {
    BtnHover(on) {
      var target = event.currentTarget;
      if (on == 1) {
        target.style.background = this.HoverTheme;
      } else {
        target.style.background = this.Theme;
      }
    },
    checkform() {
      var errors_div = document.getElementById("errors");
      var name_input = document.getElementById("name");
      var mail_input = document.getElementById("mail");
      var subject_input = document.getElementById("subject");
      var message_input = document.getElementById("message");
      //Intialise errors and input borders
      this.errors = [];
      name_input.style.border = "none";
      mail_input.style.border = "none";
      subject_input.style.border = "none";
      message_input.style.border = "none";
      if (!this.name) {
        name_input.style.border = "2px solid red";
        this.errors.push(this.$t("contactus.error_name"));
      }
      if (!this.mail) {
        mail_input.style.border = "2px solid red";
        this.errors.push(this.$t("contactus.error_mail"));
      } else if (!this.validEmail(this.mail)) {
        mail_input.style.border = "2px solid red";
        this.errors.push(this.$t("contactus.error_mail_valid"));
      }
      if (!this.subject) {
        subject_input.style.border = "2px solid red";
        this.errors.push(this.$t("contactus.error_subject"));
      }
      if (!this.message) {
        message_input.style.border = "2px solid red";
        this.errors.push(this.$t("contactus.error_message"));
      }
      if (!this.errors.length) {
        this.sendEmail(
          event,
          this.$t("contactus.send_status_ok"),
          this.$t("contactus.send_status_fail")
        );
        return true;
      } else {
        console.log(this.$t("contactus.send_status_ok"));
        errors_div.scrollIntoView({ block: "center", behavior: "smooth" });
        return false;
      }
    },
    validEmail(mail) {
      //eslint-disable-next-line
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(mail);
    },
    sendEmail(e, status_ok, status_fail) {
      var send_status = document.getElementById("send_status");
      emailjs
        .send(
          "service_typg31m",
          "template_2jrp7rm",
          {
            subject: this.subject,
            name: this.name,
            surname: this.surname,
            reply_to: this.mail,
            message: this.message
          },
          "user_0jHIRZYxF4FqunURZKQRv"
        )
        .then(
          function() {
            send_status.innerHTML = status_ok;
            send_status.style.display = "block";
            setTimeout(function() {
              send_status.style.display = "none";
            }, 8000);
            send_status.style.color = "#196719";
            send_status.style.background = "#ccffcc";
            send_status.style.border = "1px solid #196719";
            send_status.scrollIntoView({ block: "center", behavior: "smooth" });
            e.target.reset();
          },
          function() {
            send_status.innerHTML = status_fail;
            send_status.style.display = "block";
            setTimeout(function() {
              send_status.style.display = "none";
            }, 8000);
            send_status.style.color = "#cc0000";
            send_status.style.background = "#ffb3b3";
            send_status.style.border = "1px solid #cc0000";
            send_status.scrollIntoView({ block: "center", behavior: "smooth" });
          }
        );
    }
  }
};
</script>
<style scoped>
