  width: 100%;
  padding: 12px 5px;
  border: 1px solid #cc0000;
  border-radius: 6px;
  color: #cc0000;
  background-color: #ffb3b3;
  margin-bottom: 10px;
}

#send_status {
  display: none;
  padding: 12px 5px;
  margin-bottom: 10px;
  border-radius: 6px;
}

