<script>
import NavBar from "../components/navbar";
import Cover from "../components/cover";
import Footer from "../components/footer";

export default {
  components: {
    NavBar,
    Cover,
    Footer
  },
  data() {
    return {
      langs: [
        {
          id: 0,
          abr: "CA",
          src: require("../assets/lang/ca.png")
        },
        {
          id: 1,
          abr: "ES",
          src: require("../assets/lang/esp.png")
        },
        {
          id: 2,
          abr: "FR",
          src: require("../assets/lang/fr.png")
        },
        {
          id: 3,
          abr: "EN",
          src: require("../assets/lang/eng.png")
        }
      ],
      parameters: [
        {
          id: 0,
          text: "WASH",
          src: require("../assets/app/wash_symbol.png"),
          colorS: "#166483",
          colorW: "#0b3241",
          linkto: "/wash"
        },
        {
          id: 1,
          text: "EXPEDITIONS",
          src: require("../assets/app/boat_symbol.png"),
          colorS: "#cc8500",
          colorW: "#664200",
          linkto: "/expeditions"
        },
        {
          id: 2,
          text: "TEACH",
          src: require("../assets/app/teach_symbol.png"),
          colorS: "#ac2020",
          colorW: "#6c1414",
          linkto: "/teach"
        }
      ],
      about: this.$t("home.about"),
      project_text: this.$t("home.project_text")
    };
  },
  methods: {
    emphasise(event, color) {
      let servei = event.currentTarget.childNodes[0].childNodes[0];
      let button = event.currentTarget.childNodes[0].children[1];
      servei.style.borderTop = "18px solid " + color;
      servei.style.backgroundColor = "white";
      button.style.backgroundColor = color;
      button.style.borderTop = "1px solid " + color;
      button.style.opacity = "1";
    },
    noemphasise(event, color) {
      let servei = event.currentTarget.childNodes[0].childNodes[0];
      let button = event.currentTarget.childNodes[0].children[1];
      servei.style.backgroundColor = "rgba(242, 242, 242, 0.82)";
      servei.style.borderTop = "18px solid " + color;
      button.style.backgroundColor = color;
      button.style.borderTop = "1px solid " + color;
      button.style.opacity = "0.88";
    },
    showscroll(item) {
      document.getElementById(item).classList.add('about');
    }
  },
  computed: {
    comp_langs: function() {
      var langs1 = [];
      var j = 0;
      for (let i = 0; i < this.langs.length; i++) {
        if (this.$i18n.locale != this.langs[i].abr.toLowerCase()) {
          langs1[j] = {};
          langs1[j].id = j + 1;
          langs1[j].text = this.$t("langs." + this.langs[i].abr.toLowerCase());
          langs1[j].abr = this.langs[i].abr.toLowerCase();
          langs1[j].src = this.langs[i].src;
          j++;
        }
      }
      return langs1;
    }
  },
  watch: {
    comp_langs: {
      handler: function () {
        this.about = this.$t("home.about");
        this.project_text = this.$t("home.project_text");
      }
    } 
  }
};
</script>
